import React from "react";
import { Route, Routes } from "react-router-dom";

const Dashboards = React.lazy(() => import("../dashboard/dashboards"));
const Dashboard = React.lazy(() => import("../dashboard/dashboard"));
const Frameworks = React.lazy(() => import("../dashboard/frameworks"));
const Modules = React.lazy(() => import("../dashboard/components"));
const PublishedFolders = React.lazy(
  () => import("../dashboard/folders")
);
const Marketplace = React.lazy(() => import("../dashboard/marketplace"));

export default function DashboardsRoute() {
  return (
    <Routes>
        <Route path="/" element={<Dashboards />} />
        <Route path="frameworks" element={<Frameworks />} />
        <Route path="modules" element={<Modules />} />
        <Route path="folders" element={<PublishedFolders />} />
        <Route path="marketplace" element={<Marketplace />} />
        <Route path=":id" element={<Dashboard />} />
    </Routes>
  );
}
